import React, { Dispatch, SetStateAction, useCallback, useEffect } from 'react';
import { Game } from '../resources/result';
import styles from './css/leaderboard.module.css';
import { useMemoWithLabel, useStateWithLabel } from '../utils/hooks';
import { GET } from '../utils/requests';
import { TournamentStatus } from '../interfaces';

interface IndividualPlayer {
	nationality?: string
	name?: string
	team?: string
	username: string
	histories: [null, ...{
		board: number
		score: number
	}[]]
}

interface LeaderboardProps {
	status: TournamentStatus
}

export default function Leaderboard(props: LeaderboardProps) {

	const [games, setGames] = useStateWithLabel([], 'games') as [Game[][], Dispatch<SetStateAction<Game[][]>>];
	const updateGames = useCallback(() => {
		GET({
			url: ['', 'tournament', props.status.id, 'getPairings'].join('/'),
			params: {
				round: 0,
				games: true
			}
		})
			.then(res => res.map((r: {games: Game[][]}) => r.games))
			.then(setGames);
	}, [props.status.id, setGames]);
	useEffect(() => {
		updateGames();
	}, [updateGames])

	const headers: {[key: string]: {
		name: string,
		value: (p: IndividualPlayer, i?: number) => string | number
		convert?: (v: number) => string
	}} = useMemoWithLabel(() => {
		return {
			rank: {
				name: '',
				value: (p: IndividualPlayer, i: number) => i + 1,
				convert: (v: number) => '#' + v
			},
			nationality: {
				name: '',
				value: (p: IndividualPlayer) => p.nationality
			},
			name: {
				name: 'Name',
				value: (p: IndividualPlayer) => p.name || p.username
			},
			team: {
				name: 'Team',
				value: (p: IndividualPlayer) => p.team
			},
			score: {
				name: 'Score',
				value: (p: IndividualPlayer) => p.histories.reduce((acc, curr) => curr ? acc += curr.score : acc, 0)
			},
			averageBoard: {
				name: 'Av. Board',
				value: (p: IndividualPlayer) => p.histories.reduce((acc, curr) => curr ? acc += curr.board : acc, 0) / p.histories.slice(1).length,
				convert: (v: number) => v.toFixed(2)
			}
		};
	}, [], 'headers');

	const players: IndividualPlayer[] = useMemoWithLabel(() => {
		let dict = {} as {[key: string]: IndividualPlayer};
		for (let arr of games) {
			if (!arr) continue;
			if (!Array.isArray(arr)) {
				console.log(arr);
				arr = Object.values(arr);
			}
			for (let g of arr) {
				if (!dict[g.w?.username]) dict[g.w?.username] = {
					username: g.w?.username,
					team: g.w?.team,
					histories: [null, {
						board: g.board,
						score: g.w?.result
					}]
				};
				else {
					dict[g.w?.username].histories.push({
						board: g.board,
						score: g.w?.result
					});
					if (dict[g.w?.username].team && !dict[g.w?.username].team?.includes(g.w?.team)) dict[g.w?.username].team += ' ' + g.w?.team;
				};
				if (!dict[g.b?.username]) dict[g.b?.username] = {
					username: g.b?.username,
					team: g.b?.team,
					histories: [null, {
						board: g.board,
						score: g.b?.result
					}]
				};
				else {
					dict[g.b?.username].histories.push({
						board: g.board,
						score: g.b?.result
					});
					if (dict[g.b?.username].team && !dict[g.b?.username].team?.includes(g.b?.team)) dict[g.b?.username].team += ' ' + g.b?.team;
				}
			}
		}
		return Object.values(dict);
	}, [games], 'players')

	const rows = useMemoWithLabel(() => {
		return players.sort((a, b) => {
			let x = (headers.score.value(b) as number) - (headers.score.value(a) as number);
			if (x !== 0) return x;
			return (headers.averageBoard.value(a) as number) - (headers.averageBoard.value(b) as number);
		}).map((p, i) => {
			return (
				<tr>
					{Object.entries(headers).map(([k, h], j) => {
						return <td
							key={['cell', i, j].join('.')}
							className={[styles.th].join(' ')}
						>
							<div className={[styles.cell, styles.headerCell].join(' ')}>
								{h.convert ? h.convert(h.value(p, i) as number) : h.value(p, i)}
							</div>
						</td>;
					})}
				</tr>
			);
		});
	}, [headers, players], 'rows');

	return <>
		<section key='standings.1'>
			<table className={['download-table full-width', styles.table, 'standings'].join(' ')}>
				<thead className={styles.headers}>
					<tr>
						{Object.entries(headers).map(([k, h], i) => {
							return <th
								scope='column'
								key={['column', i].join('.')}
								className={[styles.th].join(' ')}
							>
								<div className={[styles.cell, styles.headerCell].join(' ')}>
									{h.name}
								</div>
							</th>;
						})}
					</tr>
				</thead>
				<tbody>				
					{rows}
				</tbody>
			</table>
		</section>
	</>;
}